const LoadButton = ({ onClick = () => {}, isLoading = true }) => {
    return isLoading ? (
        <div className={'load-btn__wrap '}>
            <Loader />
        </div>
    ) : (
        <button className={'load-btn'} onClick={onClick}>
            Load
        </button>
    );
};

const Table = ({ stats = [] }) => {
    if (!stats.length) {
        return <div>NO DATA</div>;
    }
    return stats.map((item, idx) => <TableRow data={item} idx={idx} key={item.name + idx + ''} />);
};

const TableRow = ({ data = {}, idx }) => {
    return (
        <>
            <div className="table__title">
                <div className="table__title-name">name: {data.name}</div>
                <div className="table__title-duration">total: {data.totalDuration}</div>
            </div>
            <table border={1} align={'center'} cellPadding={0} cellSpacing={0} className={'table'} key={idx + 'table'}>
                <tbody>
                    <tr className={'title'} align={'center'}>
                        <th scope={'col'}>#</th>
                        <th scope={'col'}>Code</th>
                        <th scope={'col'}>Calls</th>
                        <th scope={'col'}>Duration</th>
                        <th scope={'col'}>ACD</th>
                        <th scope={'col'}>ASR</th>
                    </tr>
                    {data.destinations.map((item, idx) => {
                        return (
                            <tr
                                align={'center'}
                                bgcolor={idx % 2 ? '#ccffff' : '#e0ebeb'}
                                key={item.destinationCode + idx}
                            >
                                <td scope={'row'}>{idx + 1}</td>
                                <td scope={'row'}>{item.destinationCode}</td>
                                <td scope={'row'}>{item.calls}</td>
                                <td scope={'row'}>{item.durationInMinutes}</td>
                                <td scope={'row'}>{item.acd}</td>
                                <td scope={'row'}>{item.asr}</td>
                            </tr>
                        );
                    })}
                </tbody>
            </table>
        </>
    );
};

const Calendars = ({}) => {
    const [startDate, setStartDate] = useState(new Date());
    const [endDate, setEndDate] = useState(new Date());
    return (
        <div className={'calendars__wrap'}>
            <div className="calendar">
                <span className={'calendar__title'}>From:</span>
                <DatePicker
                    selected={startDate}
                    onChange={date => setStartDate(date)}
                    selectsStart
                    startDate={startDate}
                    endDate={endDate}
                />
            </div>
            <div className="calendar">
                <span className={'calendar__title'}>To:</span>
                <DatePicker
                    selected={endDate}
                    onChange={date => setEndDate(date)}
                    selectsEnd
                    startDate={startDate}
                    endDate={endDate}
                    minDate={startDate}
                />
            </div>
        </div>
    );
};
